import React, { Component } from 'react'
import { Jumbotron, Button } from 'reactstrap';
import Slides from './Slides';
import Promoter from './Promoter';

  
  export default class Home extends Component {
    render() {
        return (
            <div>
                
            
                <Slides/>
                <Promoter/>
                <Jumbotron body inverse style={{ backgroundColor: '#FFB400', borderColor: '#FFB400' }}>
                    <font color="#9e1e9e"><h1 className="display-3">PROMOTION</h1></font>
                    <p className="lead">ซื้อสินค้าสก๊อตวันนี้พบสินค้าลดราคาสูงสุด 20 % พร้อมรับสิทธิประโยชน์ต่างๆมากมาย</p>
                    <hr className="my-2" />
                    <img src='/products/seven.jpg' width="500"/>
                    <h3><p>สก๊อต กระเช้า E (กระเช้า 11 ขวด) x 4</p>
                    <p>3,950.00฿ <s>4,480.00฿</s></p></h3>
                    <p className="lead">
                        <Button color="info">สั่งซื้อ</Button>
                    </p>
                </Jumbotron>
                <Jumbotron body inverse style={{ backgroundColor: '#FFB400', borderColor: '#FFB400' }}>
                    <hr className="my-2" />
                    <img src='/products/pic.jpg' width="500"/>
                    <h3><p>สก๊อต กระเช้า E (กระเช้า 11 ขวด) x 4</p>
                    <p>1,411.00฿ <s>1,590.00฿</s></p></h3>
                    <p className="lead">
                        <Button color="info">สั่งซื้อ</Button>
                    </p>
                </Jumbotron>

            </div>
  
        )
    }
}




