import React, { useState } from 'react';
import {Link} from 'react-router-dom';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText
} from 'reactstrap';

const Example = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar color="warning" light expand="md">
        <NavbarBrand href="/">reactstrap</NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="mr-auto" navbar>
            <NavItem>
              <NavLink tag={Link} to="/about/"><h4>เกี่ยวกับเรา</h4></NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={Link} to="/contact/"><h4>ติดต่อเรา</h4></NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={Link} to="/products/"><h4>สินค้า</h4></NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={Link} to="/promotion/"><h4>โปรโมชั่น</h4></NavLink>
            </NavItem>


            </Nav>
            <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink tag={Link} to="/signin/"><h4>เข้าสู่ระบบ</h4></NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={Link} to="/signup/"><h4>สมัครสมาชิก</h4></NavLink>
            </NavItem>            
          </Nav>  
        </Collapse>
      </Navbar>
    </div>
  );
}

export default Example;
